<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>欧盟数据下载管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline">
          <!-- <div class="tips">默认显示30天内数据</div> -->
          <el-form-item label="" prop="chooseDate">
            <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="egName">
            <el-input v-model="queryForm.egName" placeholder="请输入名称"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <div class="btn_cont">
            <span>
              共有 <i style="color: #f00">{{ queryForm.total }}</i> 条欧盟数据被下载，下载次数共 <i style="color: #f00">{{ count }}</i> 次。
            </span>
            <div class="choose_btn _color">
              <span @click.stop="exportData">导出当前数据</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="egName" label="标题" :show-overflow-tooltip="true"> </el-table-column>
          <el-table-column prop="author" label="作者" width="150"> </el-table-column>
          <el-table-column prop="clickNum" label="点击数" width="120"></el-table-column>
          <el-table-column prop="downloadCount" label="下载数" width="120"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">查看</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
      <!-- 详情对话框 -->
      <el-dialog title="详细信息" :visible.sync="dialogVisible" width="50%">
        <el-divider></el-divider>
        <div class="item_cont">
          <div class="item">
            <span class="left">英文标题：</span>
            <span class="right">{{ checkInfo.egName }}</span>
          </div>
          <div class="item">
            <span class="left">作者：</span>
            <span class="right">{{ checkInfo.author }}</span>
          </div>
          <div class="item">
            <span class="left">点击数：</span>
            <span class="right">{{ checkInfo.clickNum }}</span>
          </div>
          <div class="item">
            <span class="left">下载数：</span>
            <span class="right">{{ checkInfo.downloadCount }}</span>
          </div>
          <div class="item">
            <span class="left">文本日期：</span>
            <span class="right">{{ checkInfo.docDate }}</span>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click.stop="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
var res = {
  data: [],
  total: 0
}
export default {
  data() {
    return {
      queryForm: {
        egName: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        total: ''
      },
      count: '',
      tableData: [],
      checkInfo:{},
      dialogVisible: false,
    }
  },

  methods: {
    // 查看
    edit(info) {
      this.checkInfo = info
      this.httpGet('/euLegal/detail/'+info.id, '').then(res => {
        this.checkInfo.docDate = res.docDate
      })
      this.dialogVisible = true
    },

    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryForm)
    },
    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    loadData() {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      delete params['chooseDate']
      this.httpGet('/euLegal/download/query/', params).then(res => {
        this.tableData = res.rows
        this.count = res.ext1
        this.queryForm.total = res.total
      })
    },

    exportData() {
      window.open(this.$baseRequestUrl + '/euLegal/download/export?egName=' + this.queryForm.egName + '&startDate=' + this.queryForm.startDate + '&endDate=' + this.queryForm.endDate)
      window.open(this.$baseRequestUrl + '/standard/download/export?type=1&keyword=' + this.queryForm.keyword + '&startDate=' + this.queryForm.startDate + '&endDate=' + this.queryForm.endDate)
    }
  },

  mounted() {
    const now = new Date()
    const endDate = now - 8.64e7
    const startDate = now - 8.64e7 * 30
    this.queryForm.endDate = this.$moment(endDate).format('YYYYMMDD')
    this.queryForm.startDate = this.$moment(startDate).format('YYYYMMDD')
    this.queryForm.chooseDate = [startDate, endDate]
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.el-range-editor {
  width: 300px;
}
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
  .el-range-editor {
    width: 300px;
  }
}
.item {
  padding: 0 20px 20px;
  display: flex;
  .left {
    flex: 0 0 auto;
    width: 70px;
    text-align: right;
  }
}
</style>
