<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item v-if="affirmInfo.type === 'check'" :to="{ path: '/affirmCheck' }">标准查看</el-breadcrumb-item>
        <el-breadcrumb-item v-if="affirmInfo.type === 'conf1'" :to="{ path: '/affirm1' }">标准确认1</el-breadcrumb-item>
        <el-breadcrumb-item v-if="affirmInfo.type === 'conf2'" :to="{ path: '/affirm2' }">标准确认2</el-breadcrumb-item>
        <el-breadcrumb-item v-if="affirmInfo.type === 'conf3'" :to="{ path: '/audit' }">标准审核</el-breadcrumb-item>
        <el-breadcrumb-item>标准确认详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 基本信息 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <div class="grid-content bg-purple">
            <span>委托单位：</span>
            {{ affirmInfo.company }}
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple">
            <span>委托人：</span>
            {{ affirmInfo.contractName }}
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple">
            <span>联系方式：</span>
            {{ affirmInfo.contractMethod }}
          </div>
        </el-col>
      </el-row>
      <div class="MT_table_cont item">
        <!-- 按钮 -->
        <div class="btns">
          <el-button v-if="affirmInfo.type === 'conf1' || affirmInfo.type === 'conf2'" @click.stop="search">重新检索</el-button>
          <el-button v-if="affirmInfo.type === 'conf1'" @click.stop="save">保存</el-button>
          <el-button v-if="affirmInfo.type === 'conf2' || affirmInfo.type === 'conf3'" type="warning" @click.stop="repluse">打回</el-button>
          <el-button v-if="affirmInfo.type === 'conf1' || affirmInfo.type === 'conf2'" type="success" @click.stop="submit">提交</el-button>
          <el-button v-if="affirmInfo.type === 'conf3'" type="success" @click.stop="approver">批准</el-button>
          <el-button v-if="affirmInfo.type === 'check'&& affirmInfo.flag==='approve'" @click.stop="exportDataDialog">导出数据</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <!-- <span @click.stop="exportData">导出数据</span> -->
            </div>
          </div>
        </div>
        <el-table border id="detail" :data="tableData" style="width: 100%">
          <el-table-column prop="rownum_" label="序号" width="60">
          {{affirmInfo.rownum_}}
          </el-table-column>
          <el-table-column label="申请确认标准(方法)名称及编号(含年号)">
            <template slot-scope="scope">
              <div class="cont">
                <div class="name">
                  <span
                    @dblclick="dblclick(scope.$index, scope.$index, 'originalName', 'editOriginName', 'originalName')"
                    v-show="!scope.row.editOriginName"
                    :ref="'originalName' + scope.$index + '_' + scope.$index"
                  >
                    {{ scope.row.originalName }}
                  </span>
                  <el-input @blur="scope.row.editOriginName = false" v-model="scope.row.originalName" v-show="scope.row.editOriginName"></el-input>
                </div>
                <div class="code">
                  <span
                    @dblclick="dblclick(scope.$index, scope.$index, 'originalNo', 'editOrigincode', 'originalNo')"
                    v-show="!scope.row.editOrigincode"
                    :ref="'originalNo' + scope.$index + '_' + scope.$index"
                  >
                    {{ scope.row.originalNo }}
                  </span>
                  <el-input @blur="scope.row.editOrigincode = false" v-model="scope.row.originalNo" v-show="scope.row.editOrigincode"></el-input>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="检索结果标准(方法)名称及编号(含年号)">
            <template slot-scope="scope">
              <div class="cont" v-for="(item, index) in scope.row.resultNameAndNo" :key="index">
                <div>
                  <span @dblclick="dblclick(scope.$index, index, 'resultName', 'editName', 'resultNameAndNo')" v-show="!item.editName" :ref="'resultName' + scope.$index + '_' + index">
                    {{ item.resultName }}
                  </span>
                  <el-input @blur="item.editName = false" v-model="item.resultName" v-show="item.editName"></el-input>
                </div>
                <div>
                  <span @dblclick="dblclick(scope.$index, index, 'resultNo', 'editCode', 'resultNameAndNo')" v-show="!item.editCode" :ref="'resultNo' + scope.$index + '_' + index">
                    {{ item.resultNo }}
                  </span>
                  <el-input @blur="item.editCode = false" v-model="item.resultNo" v-show="item.editCode"></el-input>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="确认结论" width="80">
            <template slot-scope="scope">
              <div v-if="affirmInfo.type === 'check'">{{ scope.row.validateType }}</div>
              <el-select v-else v-model="scope.row.validateType" placeholder="请选择确认结论">
                <el-option label="A" value="A"></el-option>
                <el-option label="B" value="B"></el-option>
                <el-option label="E" value="E"></el-option>
                <el-option label="F" value="F"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="备注" width="300">
            <template slot-scope="scope">
              <div v-if="affirmInfo.type === 'check'">{{ scope.row.comments }}</div>
              <el-input v-else type="textarea" v-model="scope.row.comments" rows="2"></el-input>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 序号对话框 -->
      <el-dialog title="序号" :visible.sync="reportSequenceVisible" width="30%">
        <el-form :model="exportForm">
          <el-form-item label="序号" :label-width="formLabelWidth">
            <el-input v-model="exportForm.reportSequence" placeholder="请输入序号"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="reportSequenceVisible = false">取 消</el-button>
          <el-button type="primary" @click="exportData">确 定</el-button>
        </div>
      </el-dialog>

    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      affirmInfo: {},
      edit: false,
      reportSequenceVisible:false,
      formLabelWidth: '120px',
      exportForm:{
        reportSequence:''
      }
    }
  },
  methods: {
    // 重新检索
    search() {
      this.affirmInfo.status = 0
      this.loadData('create')
    },
    // 打回
    repluse() {
      this.httpPost('/standard/ack/result/reject/'+this.affirmInfo.id, '').then(ret => {
        this.$message({ type: 'success', message: '打回成功!' })
        this.$router.back()
      })
    },
    // 批准
    approver() {
      let ackIdList =[]
      ackIdList.push(this.affirmInfo.id)
      this.httpPost('/standard/ack/result/approve', ackIdList).then(ret => {
        this.$message({ type: 'success', message: '批准成功!' })
        this.$router.back()
      })
    },
    // 保存
    save() {
      this.tableData.resultNoAndName=this.tableData.resultNameAndNo
      let data = JSON.parse(JSON.stringify(this.tableData))
      data.map(item => (item.resultNoAndName = this.objArr2str(item.resultNameAndNo)))
      console.log(data)
      this.httpPost('/standard/ack/result/save/'+this.affirmInfo.id, data).then(ret => {
        this.$message({ type: 'success', message: '保存成功!' })
        this.loadData('check')
      })
    },
    // 提交
    submit() {
      this.tableData.resultNoAndName=this.tableData.resultNameAndNo
      let data = JSON.parse(JSON.stringify(this.tableData))
      data.map(item => (item.resultNoAndName = this.objArr2str(item.resultNameAndNo)))
      console.log(data)
      if( this.affirmInfo.type === 'conf1'){
        this.httpPost('/standard/ack/result/confirm/'+this.affirmInfo.id, data).then(ret => {
          this.$message({ type: 'success', message: '提交成功!' })
          this.loadData('confirm')
        })
      }else{
        this.httpPost('/standard/ack/result/check/'+this.affirmInfo.id, data).then(ret => {
          this.$message({ type: 'success', message: '提交成功!' })
          this.loadData('check')
        })
      }
    },

    // 双击修改
    dblclick(i, l, el, flag, key) {
      if (this.affirmInfo.type === 'check') return false
      //  表格下标 数组下表 dom 名称/代码 待确认/已确认
      // debugger
      key === 'resultNameAndNo' ? (this.tableData[i][key][l][flag] = true) : (this.tableData[i][flag] = true)

      this.$nextTick(() => {
        // console.log(this.$refs[el + i + '_' + l])
        key === 'resultNameAndNo' ? this.$refs[el + i + '_' + l][0].nextSibling.children[0].focus() : this.$refs[el + i + '_' + l].nextSibling.children[0].focus()
      })
    },

    exportData() {
      window.open(this.$baseRequestUrl + '/standard/ack/result/report/download/' +this.affirmInfo.id+"?reportSequence="+this.exportForm.reportSequence)
    },
    exportDataDialog() {
     this.reportSequenceVisible = true
      this.httpGet('/standard/ack/result/report/sequence/' + this.affirmInfo.id, '').then(res => {
          console.log(res)
          this.exportForm.reportSequence=res.reportSequence
        })
    },

    loadData(flag) {
      if(flag==='create'&&this.affirmInfo.status!=1){
        this.httpGet('/standard/ack/result/compare/' + this.affirmInfo.id, '').then(res => {
          // console.log(res)
          var data = Object.assign([], res)
          data.map(i => {
            i.resultNameAndNo = this.str2objArr(i.resultNameAndNo)
            i.editOriginName = false
            i.editOrigincode = false
          })
          // console.log(data)
          this.tableData = data
        })
      }else{
        this.httpGet('/standard/ack/result/query/' + this.affirmInfo.id, '').then(res => {
          // console.log(res)
          var data = Object.assign([], res)
          data.map(i => {
            i.resultNameAndNo = this.str2objArr(i.resultNameAndNo)
            i.editOriginName = false
            i.editOrigincode = false
          })
          // console.log(data)
          this.tableData = data
        })
      }
    },

    str2objArr(str) {
      if(!str){
        str = '暂无'
      }
      var jsonArr = []
      if (str.indexOf('|') === -1) {
        var temp = str.split('%')
        jsonArr.push({ resultName: temp[0], resultNo: temp[1], editName: false, editCode: false })
      } else {
        var arr = str.split('|')
        arr.map(i => {
          var temp = i.split('%')
          jsonArr.push({ resultName: temp[0], resultNo: temp[1], editName: false, editCode: false })
        })
      }
      return jsonArr
    },
    objArr2str(objArr) {
      // console.log(objArr)
      var str = ''

      objArr.map(item => {
        // console.log(item)
        // alert(item.resultNo)
        if(item.resultNo){
          str += item.resultName + '%' + item.resultNo + '|'
        }else{
          str += item.resultName  + '|'
        }
      })
      return str.slice(0, str.length - 1)
    }
  },
  mounted() {
    this.affirmInfo = this.$sessionData('get', 'affirmInfo')
    this.loadData(this.affirmInfo.flag)
    console.log(this.affirmInfo)
  }
}
</script>

<style lang="scss" scoped>
.el-row {
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
  margin-left: 0 !important;
  margin-right: 0 !important;
}
.grid-content {
  height: 50px;
  line-height: 50px;
}
.item {
  border: none;
  padding: 0;
  // margin-top: -10px;
}
.el-select {
  width: 100%;
}
/deep/.el-table {
  .cell {
    text-align: center;
  }
}
.cont {
  span {
    // height: 32px;
    // line-height: 32px;
  }
}
</style>
