<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standard' }">标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>指标管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove">批量删除</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="indexName" label="指标名称" width="100"> </el-table-column>
          <el-table-column prop="indexValue" label="指标值"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      queryForm: {
        size: 10,
        page: 1,
        total: '',
        standardId:''
      }
    }
  },

  methods: {

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    add() {
      this.$router.replace({ path: '/editTarget', name: 'EditTarget', params: { type: 'add',standardId: this.queryForm.standardId } })
    },

    // 修改
    edit(info) {
      this.$router.replace({ path: '/editTarget', name: 'EditTarget', params: { type: 'edit', info } })
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let indexIdList =[]
            indexIdList.push(id)
          this.httpPost('/standard/index/index/rem', indexIdList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let indexIdList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            indexIdList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/standard/index/index/rem', indexIdList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/standard/index/index/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.queryForm.standardId = this.$route.params.info.id
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
