<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>标准确认管理</el-breadcrumb-item>
        <el-breadcrumb-item>标准确认查看</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="委托单位名称" prop="keyword">
            <el-input v-model="queryForm.keyword" placeholder="请输入搜索内容" clearable></el-input>
          </el-form-item>
          <!-- <div class="tips">默认显示30天内数据</div> -->
          <el-form-item label="" prop="chooseDate">
            <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <!-- <el-button @click.stop="add" icon="el-icon-plus">新增</el-button> -->
            <span> 共有 <i style="color: #f00">{{ queryForm.total }}</i> 个标准确认单，确认的标准个数共 <i style="color: #f00">{{ count }}</i> 个。 </span>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="exportData">导出当前数据</span>
            </div>
          </div>
        </div>

        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
          <!--<el-table-column prop="rownum_" label="序号" width="100"> </el-table-column>-->
          <el-table-column prop="company" label="委托单位"> </el-table-column>
          <el-table-column prop="confirmNumber" label="确认委托个数"> </el-table-column>
          <el-table-column prop="contractName" label="委托人"> </el-table-column>
          <el-table-column prop="contractMethod" label="联系方式"> </el-table-column>
          <el-table-column prop="submitDate" label="委托日期"> </el-table-column>
          <el-table-column label="状态" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.flag === 'confirm'">已检索</el-tag>
              <el-tag v-if="scope.row.flag === 'create'" type="danger">已创建</el-tag>
              <el-tag v-if="scope.row.flag === 'check'" type="warning">已审核</el-tag>
              <el-tag v-if="scope.row.flag === 'approve'" type="success">已批准</el-tag>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="80">
            <template slot-scope="scope">
              <el-link v-if="scope.row.status===1" :underline="false" type="primary" @click.stop="edit(scope.row)">查看</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <el-dialog title="标准确认" :visible.sync="dialogVisible" width="80%">
        <el-divider></el-divider>

        <!-- 按钮 -->

        <div class="MT_table_cont item">
          <div class="btns">
            <!-- <el-button @click.stop="add" icon="el-icon-plus">新增</el-button> -->
            <div class="btn_cont">
              <div class="choose_btn _color">
                <span @click.stop="exportData">导出数据</span>
              </div>
            </div>
          </div>
        </div>
        <!-- 表格 -->
        <table-data></table-data>
        <!-- 表格 -->

        <!-- <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button> -->
        <!-- <el-button type="primary" @click="detailExport">导 出</el-button> -->
        <!-- </span> -->
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import tableData from '@/components/confirm/Confirm'
export default {
  components: { tableData },
  data() {
    return {
      queryForm: {
        keyword: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        total: '',
        roleCode: '10001'
      },
      count: '',
      tableData: [],
      tableDetail: [],
      dialogVisible: false,
      detailPort: false, // 是否可以导出

      flag: '' // create-创建，confirm-已检索，check-已审核，approve-已批准
    }
  },

  methods: {
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      // console.log(this.queryForm)
    },
    onSubmit() {
      // console.log(this.queryForm)
      this.loadData()
    },

    // add() {
    //   this.$router.push({ path: '/editStandard', name: 'EditStandard', params: { type: 'add' } })
    // },

    // 查看检索
    edit(info) {
      // this.info = info
      // this.dialogVisible = true
      // this.loadDetail(info.id)
      info.type = 'check'
      this.$router.push({ path: '/affirmDetail', name: 'AffirmDetail', params: { info: info } })
      this.$sessionData('set', 'affirmInfo', info)
    },

    loadData() {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate

      console.log(params)
      this.httpGet('/standard/ack/query', params).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.count = res.ext1
        this.queryForm.total = res.total
      })
    },

    // loadDetail(id) {
    //   this.httpGet('/standard/ack/result/query/' + id, {}).then(res => {
    //     console.log(res)
    //     this.detailPort = res.length ? true : false
    //     this.tableDetail = res
    //   })
    // },
    // 列表导出
    exportData() {
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      params.total = ''
      params.page = ''
      params.size = ''
      let paramArr = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/standard/ack/export?' + paramArr.join('&'))
    }
    // 查看导出
    // detailExport() {
    //   if (!this.detailPort) return this.$message.warning('暂无数据导出'), false
    //   window.open(this.$baseRequestUrl + '/standard/ack/result/report/download/1?reportSequence=2')
    //   this.dialogVisible = false
    // }
  },

  mounted() {
    const now = new Date()
    const endDate = now
    const startDate = now - 8.64e7 * 30

    this.queryForm.endDate = this.$moment(endDate).format('YYYYMMDD')
    this.queryForm.startDate = this.$moment(startDate).format('YYYYMMDD')
    this.queryForm.chooseDate = [startDate, endDate]
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.tips {
  display: inline-block;
  line-height: 40px;
  color: #bbb;
  margin-right: 20px;
}
.el-range-editor {
  width: 300px;
}
/deep/#detail {
  .cell {
    text-align: center;
  }
}
.item {
  border: none;
  padding: 0;
  padding-bottom: 10px;
}
</style>
