<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standard' }">标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改标准' : '新增标准' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="标准编号" prop="code">
          <el-input v-model="ruleForm.code" placeholder="请输入标准编号"></el-input>
        </el-form-item>
        <el-form-item label="中文题名" prop="csName">
          <el-input v-model="ruleForm.csName" placeholder="请输入中文题名"></el-input>
        </el-form-item>
        <el-form-item label="英文题名" prop="egName">
          <el-input v-model="ruleForm.egName" placeholder="请输入英文题名"></el-input>
        </el-form-item>
        <el-form-item label="发布年代" prop="year">
          <el-date-picker style="width: 300px" size="small" v-model="ruleForm.year" type="year" placeholder="请选择年份"> </el-date-picker>
        </el-form-item>
        <el-form-item label="实施日期" prop="executeDate">
          <el-date-picker style="width: 300px" size="small" v-model="ruleForm.executeDate" type="date" placeholder="请选择日期"> </el-date-picker>
        </el-form-item>
        <el-form-item label="有效日期" prop="expiryDate">
          <el-date-picker style="width: 300px" size="small" v-model="ruleForm.expiryDate" type="date" placeholder="请选择日期"> </el-date-picker>
        </el-form-item>
        <el-form-item label="全文页数" prop="page">
          <el-input v-model.number="ruleForm.page" placeholder="请输入全文页数"></el-input>
        </el-form-item>
        <el-form-item label="标准状态" prop="status">
          <el-select v-model="ruleForm.status" placeholder="请选择标准状态">
            <el-option label="现形" value="A"></el-option>
            <el-option label="作废" value="W"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="标准类型" prop="type">
          <el-select v-model="ruleForm.type" placeholder="请选择标准类型">
            <el-option label="国家标准" value="1"></el-option>
            <el-option label="行业标准" value="2"></el-option>
            <el-option label="地方标准" value="3"></el-option>
            <el-option label="国际标准" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="中国标准分类号" prop="cnVal">
          <el-input disabled v-model="ruleForm.cnVal"></el-input>
          <el-link :underline="false" @click.stop="loadCascader(1)">{{ type == 1 ? '添加' : '修改' }}</el-link>
          <!-- <el-cascader v-model="ruleForm.csdc" :options="cnStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleCnChange"></el-cascader> -->
        </el-form-item>
        <el-form-item label="国际标准分类号" prop="inVal">
          <el-input disabled v-model="ruleForm.inVal"></el-input>
          <el-link :underline="false" @click.stop="loadCascader(2)">{{ type == 1 ? '添加' : '修改' }}</el-link>
          <!-- <el-cascader v-model="ruleForm.inStClassifyAdm" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item label="国民经济分类号" prop="necVal">
          <el-input disabled v-model="ruleForm.necVal"></el-input>
          <el-link :underline="false" @click.stop="loadCascader(3)">{{ type == 1 ? '添加' : '修改' }}</el-link>
          <!-- <el-cascader v-model="ruleForm.inStClassifyAdm" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item label="代替的标准" prop="replaceSd">
          <el-input v-model="ruleForm.replaceSd" placeholder="请输入标识"></el-input>
        </el-form-item>
        <el-form-item label="哪些标准代替" prop="replacedSd">
          <el-input v-model="ruleForm.replacedSd" placeholder="请输入被代替的标准"></el-input>
        </el-form-item>
        <el-form-item label="适用范围" prop="sdRange">
          <el-input type="textarea" v-model="ruleForm.sdRange"></el-input>
        </el-form-item>
        <el-form-item label="标识" prop="sign">
          <el-input v-model="ruleForm.sign" placeholder="请输入代替的标准"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
          <el-button @click="resetForm('ruleForm')">返回</el-button>
        </el-form-item>
      </el-form>

      <!-- 分类选择 -->

      <el-dialog title="分类选择" :visible.sync="casLog" width="30%">
        <el-divider></el-divider>
        <el-cascader v-show="isCn === 1" :show-all-levels="false" @change="handleCnChange" v-model="ruleForm.csdc" ref="cascaderHandle" :props="cnprops" clearable></el-cascader>
        <el-cascader v-show="isCn === 2" :show-all-levels="false" @change="handleInChange" v-model="ruleForm.ics" ref="cascaderHandle" :props="inprops" clearable></el-cascader>
        <el-cascader v-show="isCn === 3" :show-all-levels="false" @change="handleNecChange" v-model="ruleForm.nec" ref="cascaderHandle" :props="necprops" clearable></el-cascader>

        <span slot="footer" class="dialog-footer">
          <el-button @click="casLog = false">取 消</el-button>
          <el-button type="primary" @click="casLog = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      ruleForm: {
        code: '',
        id: '',
        csName: '',
        egName: '',
        type: '', //  '' 全部  1行业 2地方 3国家 4国际
        standText: '', //  '' 全部  0包含 1不包含
        status: '', // '' 全部  0作废 ''现形
        year: '',
        executeDate: '',
        expiryDate: '',
        csdc: '',
        ics: '',
        nec:'',
        replaceSd: '',
        replacedSd: '',
        sdRange: '',
        sign: '',
        cnVal: '',
        inVal: '',
        necVal:'',
      },
      // cnStAdmOptions: [], // 中国标准分类管理
      // inStAdmOptions: [], // 国际标准分类号
      rules: {
        code: [
          { required: true, message: '请输入标准编号', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ]
      },

      type: '',
      casLog: false,
      cnprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.cnProps
      },
      inprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.inProps
      },
      necprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.necProps
      },
      innodes: [],
      cnnodes: [],
      necnodes: [],
      isCn: 1 //  1cn  2in
    }
  },
  methods: {
    loadCascader(n) {
      this.casLog = true
      this.isCn = n
    },

    // 中国标准改变
    handleCnChange(value) {
      // console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.cnnodes.map(item => {
        if (item.value === id) {
          this.ruleForm.csdc = item.value
          this.ruleForm.cnVal = item.label
        }
      })
      this.hiddenDom(1)
    },

// 英文标准改变
    handleInChange(value) {
      // console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.innodes.map(item => {
        if (item.value === id) {
          this.ruleForm.ics = item.value
          this.ruleForm.inVal = item.label
        }
      })
      this.hiddenDom(1)
    },

    // 国民经济标准改变
    handleNecChange(value) {
      // console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.necnodes.map(item => {
        if (item.value === id) {
          this.ruleForm.nec = item.value
          this.ruleForm.necVal = item.label
        }
      })
      this.hiddenDom(1)
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.ruleForm)
          let params = Object.assign({}, this.ruleForm)
          params.executeDate = this.$moment(this.ruleForm.executeDate).format('YYYY-MM-DD')
          params.expiryDate = this.$moment(this.ruleForm.expiryDate).format('YYYY-MM-DD')
          params.year = this.$moment(this.ruleForm.year).format('YYYY')
          if (this.$route.params.type === 'edit') {
            this.httpPost('/standard/mod/' + params.id, params).then(res => {
              this.$message.success('修改成功')
              this.resetForm('ruleForm')
            })
          } else {
            this.httpPost('/standard/add', params).then(res => {
              this.$message.success('增加成功')
              this.resetForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    },

    loadData() {},
    // getReplace() {
    //   let params = {
    //     type: 1
    //   }
    //   this.httpGet('/standard/cate/query/', params).then(res => {
    //     this.cnStAdmOptions = res
    //   })
    //   params['type'] = 2
    //   this.httpGet('/standard/cate/query/', params).then(res => {
    //     this.inStAdmOptions = res
    //   })
    // },

    cnProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 1 }
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
          this.cnnodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    inProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 2 }
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
          this.innodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    necProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 3 }
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
          this.necnodes.push({ value: item.fullCode, label: item.cateName,cateId:item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    hiddenDom(type) {
      setTimeout(() => {
        if (type === 1) {
          var el = document.getElementsByClassName('el-scrollbar')
          var doms = el[el.length - 1].getElementsByTagName('div')
          for (let i = 0; i < doms.length; i++) {
            const e = doms[i]
            if (e.innerHTML === '暂无数据') {
              el[el.length - 1].style.display = 'none'
            }
          }
        } else {
          var el = document.getElementsByClassName('el-scrollbar')
          for (let i = 0; i < el.length; i++) {
            el[i].style.display = 'block'
          }
        }
      }, 0)
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      this.httpGet('/standard/detail/'+params.info.id, '' ).then(res => {
        this.ruleForm = res
        this.ruleForm. cnVal = res.csdcName
        this.ruleForm. inVal = res.icsName
        this.ruleForm. necVal = res.necName
        this.ruleForm.csdc = res.csdc
        this.ruleForm.ics = res.ics
        this.ruleForm.nec = res.nec
        this.ruleForm.type = this.ruleForm.type.toString()
      })
      console.log(this.ruleForm)
      this.type = 2
    } else {
      this.type = 1
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .el-input {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
</style>
