<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>标准托管管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="" prop="status">
            <el-select v-model="queryForm.keywordType" placeholder="请选择关键词类型">
              <el-option label="标准编号或标准名" value="1"></el-option>
              <el-option label="机构名称" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="keyword">
            <el-input v-model="queryForm.keyword" placeholder="请输入搜索内容"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <div class="btn_cont">
            <span>
              共有 <i style="color: #f00">{{ queryForm.total }}</i> 个用户托管，托管标准共 <i style="color: #f00">{{ count }}</i> 个。
            </span>
            <div class="choose_btn _color">
              <span @click.stop="exportData">导出当前数据</span>
            </div>
          </div>
        </div>

        <!-- 表格列表 -->
        <el-table :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="loginName" label="机构代码/用户名" width="150"> </el-table-column>
          <el-table-column prop="department" label="机构名称"> </el-table-column>
          <el-table-column label="用户类型" width="120">
            <template slot-scope="scope">
              {{ scope.row.userType === 0 ? 'KEY用户' : '定制用户' }}
            </template>
          </el-table-column>
          <el-table-column prop="focusNum" label="托管标准个数" width="120"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row.userId)">查看</el-link>
              <el-link :underline="false" type="primary" @click.stop="exportDetail(scope.row.userId)">导出</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <el-dialog title="托管详情" :visible.sync="dialogVisible" width="50%">
        <el-divider></el-divider>
        <el-table :data="tableDetail" style="width: 100%">
          <el-table-column prop="code" label="标准编号" width="180"> </el-table-column>
          <el-table-column prop="csName" label="标准名称"> </el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      trusteUserCount: 0,
      // trusteNum: 0,
      queryForm: {
        keyword: '',
        size: 10,
        page: 1,
        total: '',
        keywordType: '1'
      },
      count: '',
      queryFormDetail: {
        size: 10,
        page: 1,
        total: '',
        userId: ''
      },
      tableData: [],
      tableDetail: [],
      dialogVisible: false
    }
  },

  methods: {
    exportDetail(id) {
      window.open(this.$baseRequestUrl + '/standard/managed/detail/export?userId=' + id)
    },
    onSubmit() {
      this.loadData()
    },

    // 查看
    edit(id) {
      this.dialogVisible = true
      this.loadDetail(id)
    },

    loadData() {
      console.log('1')
      this.queryForm.total = ''
      this.httpGet('/standard/managed/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
        this.count = res.ext1
        this.focusNum = res.focusNum
      })
      // this.trusteNum = res.trusteNum
    },

    loadDetail(id) {
      this.queryFormDetail.total = ''
      this.queryFormDetail.userId = id
      this.httpGet('/standard/managed/detail', this.queryFormDetail).then(res => {
        this.tableDetail = res.rows
        this.queryFormDetail.total = res.total
      })
    },

    exportData() {
      window.open(this.$baseRequestUrl + '/standard/managed/export')
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
