<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standard' }">标准管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/targetManege' }">指标管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'add' ? '新增指标' : '修改指标' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="标题" prop="indexName">
          <el-input size="small" style="width: 300px" v-model="ruleForm.indexName"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="indexValue">
          <Tinymce style="width: 60%" ref="bzlc" :id="'tinymceBzlc'" @getCont="getCont"></Tinymce>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm('ruleForm')">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce/Tinymce'
export default {
  components: { Tinymce },
  data() {
    return {
      ruleForm: {
        indexName: '',
        indexValue: '',
        standardId:''
      },
      rules: {
        indexName: [
          { required: true, message: '请输入标题名称', trigger: 'blur' },
          { min: 3, max: 99, message: '长度在 3 到 30 个字符', trigger: 'blur' }
        ],
        indexValue: [
          { required: true, message: '请输入内容', trigger: 'blur' },
          { min: 3, max: 5000, message: '长度在 3 到 5000 个字符', trigger: 'blur' }
        ]
      }
    }
  },

  methods: {
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if(this.$route.params.type === 'add'){
            this.httpPost("/standard/index/index/add",this.ruleForm).then(res => {
              this.$message.success('增加成功')
              this.cancelForm(formName)
            })
          }else{
            this.httpPost("/standard/index/index/mod/"+this.ruleForm.id,this.ruleForm).then(res => {
              this.$message.success('修改成功')
              this.cancelForm(formName)
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 返回
    cancelForm(formName) {
      this.$refs[formName].resetFields()
      let info = {
        id:this.ruleForm.standardId
      }
      this.$router.replace({ path: '/targetManege', name: 'TargetManege', params: { info } })
    },

    // 富文本
    getCont(val) {
      this.ruleForm.indexValue = val
    }
  },

  mounted() {
    const { params } = this.$route
    if (params.type === 'edit') {
      this.ruleForm = params.info
      this.$refs.bzlc.editorValue = params.info.indexValue
    }else{
      this.ruleForm.standardId = params.standardId
    }
  }
}
</script>

<style lang="scss" scoped></style>
