<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>题录库文件上传</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form label-width="120px">
        <el-form-item label="题录库文件">
          <el-upload class="upload-demo" :action="uploadAction()" :on-success="uploadSuccessMdb" :beforeUpload="beforeExcelUpload">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><i class="el-icon-upload2"></i> 上传</div>
          </el-upload>
          <div>
            <span>请上传题录库mdb文件</span>
          </div>
          <div v-if="mdbFileName" class="tips2">
            <span >{{mdbFileName}}</span><el-link type="primary" style="margin-left: 20px" @click="importMDB">开始录入</el-link>
          </div>
        </el-form-item>
        <el-form-item label="地方标准文件">
          <el-upload class="upload-demo" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeExcelUpload">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><i class="el-icon-upload2"></i> 上传</div>
          </el-upload>
          <div class="tips2">
            <span>请上传地方标准excel文件！</span>
            <span>excel模板下载：</span>
            <el-link type="primary" @click="downLoadExcel">地方标准模板.xlxs</el-link>
          </div>
          <div v-if="fileName" class="tips2">
            <span >{{fileName}}</span><el-link type="primary" style="margin-left: 20px" @click="importExcel">开始录入</el-link>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mdbFileName:'',
      mdbFileUrl:'',
      fileName:'',
      fileUrl:''
    }
  },

  methods: {
    // 下载表格
    downLoadExcel() {
      window.open(this.$templateDownloadUrl+'TEMPLATE2')
      // window.open('http://192.168.1.3:9004/file/download?fileName=批量修改模板.xls')
    },

    uploadAction() {
      return this.$ExcelUpload
      // return ''
    },

    uploadSuccessMdb(res, file, fileList) {
      console.log(res, file, fileList)
      this.mdbFileName =res.data.fileName
      this.mdbFileUrl =res.data.fileUrl
    },
    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.fileName =res.data.fileName
      this.fileUrl =res.data.fileUrl
    },
    importMDB(){
      this.httpGet('/standard/importMDB', {fileUrl:this.mdbFileUrl},1).then(res => {
        this.$message({ type: 'success', message: '录入成功!' })
        this.mdbFileName =''
        this.mdbFileUrl =''
      })
    },
    importExcel(){
      this.httpGet('/standard/importLocal', {fileUrl:this.fileUrl},1).then(res => {
        this.$message({ type: 'success', message: '录入成功!' })
        this.fileName =''
        this.fileUrl =''
      })
    },

    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: '上传文件大小不能超过 100MB!',
          type: 'warning'
        })
      }
      return isLt2M
    }
  }
}
</script>

<style lang="scss" scoped>
.tips2 {
  display: flex;
  .el-form-item__content {
    line-height: 20px;
  }
}
/deep/.upload-demo {
  width: 60px;
  height: 32px;
  margin-bottom: 10px;
  .el-upload-list {
    width: 486px;
  }
}
/deep/.el-upload--text {
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 32px;
  border: 1px dashed #ddd;
  border-radius: 4px;
  .el-upload__text {
    font-size: 12px;
  }
  .el-icon-upload {
    display: none;
  }
}
</style>
