<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="140px">
        <el-form-item label="关键字查询" prop="keyWord">
          <el-input v-model="queryForm.keyWord" placeholder="请输入标准编号 / 中文名称 / 英文名称"></el-input>
        </el-form-item>
        <el-form-item label="标准编号" prop="code">
          <el-input v-model="queryForm.code" placeholder="请输入标准编号"></el-input>
        </el-form-item>
        <el-form-item label="标准类型" prop="type">
          <el-select v-model="queryForm.type" placeholder="请选择标准类型">
            <el-option label="国家标准" value="1"></el-option>
            <el-option label="行业标准" value="2"></el-option>
            <el-option label="地方标准" value="3"></el-option>
            <el-option label="国际标准" value="4"></el-option>
            <el-option label="团体标准" value="5"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="标准文本" prop="ppbj">
          <el-select v-model="queryForm.ppbj" placeholder="请选择标准文本">
            <el-option label="包含" value="1"></el-option>
            <el-option label="不包含" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="标准状态" prop="status">
          <el-select v-model="queryForm.status" placeholder="请选择标准状态">
            <el-option label="全部" value=""></el-option>
            <el-option label="现形" value="A"></el-option>
            <el-option label="作废" value="W"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-show="isHandPick" label="发行年代" prop="startYear">
          <el-date-picker style="width: 300px" size="small" v-model="queryForm.startYear" @change="dateChange($event, 0)" type="year" placeholder="请选择年份"> </el-date-picker>
        </el-form-item>
        <el-form-item v-show="isHandPick" label="实施日期" prop="startExecuteDate">
          <el-date-picker style="width: 300px" size="small" v-model="queryForm.startExecuteDate" @change="dateChange($event, 1)" type="date" placeholder="请选择日期"> </el-date-picker>
        </el-form-item>
        <el-form-item v-show="isHandPick" label="中国标准分类号" prop="csdc">
          <el-cascader :show-all-levels="false" @change="handleCnChange" v-model="queryForm.csdc" ref="cascaderHandle" :props="cnprops" clearable></el-cascader>
          <!-- <el-cascader v-model="queryForm.csdc" :options="cnStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleCnChange"></el-cascader> -->
        </el-form-item>
        <el-form-item v-show="isHandPick" label="国际标准分类号" prop="ics">
          <el-cascader :show-all-levels="false" @change="handleInChange" v-model="queryForm.ics" ref="cascaderHandle" :props="inprops" clearable></el-cascader>
          <!-- <el-cascader v-model="queryForm.ics" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item v-show="isHandPick" label="国民经济分类号" prop="nec">
          <el-cascader :show-all-levels="false" @change="handleNeChange" v-model="queryForm.nec" ref="cascaderHandle" :props="necprops" clearable></el-cascader>
          <!-- <el-cascader v-model="queryForm.ics" :options="inStAdmOptions" :props="{ checkStrictly: true }" clearable @change="handleInChange"></el-cascader> -->
        </el-form-item>
        <el-form-item v-show="isHandPick" label="标识" prop="sign">
          <el-input v-model="queryForm.sign" placeholder="请输入标识"></el-input>
        </el-form-item>
        <el-form-item class="select_btn">
          <div class="choose_condition" @click="chooseCondition">{{ isHandPick ? '精选筛选条件 ∧' : '更多筛选条件 ∨' }}</div>
          <el-button type="primary" @click="onSubmit">搜索</el-button>
          <el-button @click="resetForm('queryForm')">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove">批量删除</span>
              <span @click.stop="push">批量推送</span>
              <span @click.stop="identify">批量标识</span>
              <span @click.stop="clearIdentify">清除标识</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="code" label="标准号" width="220">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" style="cursor: pointer" @click="detail(scope.row.id)">
                {{ scope.row.code }}
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="csName" label="标准名称"> </el-table-column>
          <el-table-column label="状态" width="120">
            <template slot-scope="scope">
              {{ scope.row.status === 'A' ? '现行' : '作废' }}
            </template>
          </el-table-column>
          <el-table-column label="标准文本" width="120">
            <template slot-scope="scope">
              {{ scope.row.ppbj === 0 ? '不包含' : '包含' }}
            </template>
          </el-table-column>
          <el-table-column prop="year" label="发布年代" width="120"> </el-table-column>
          <el-table-column prop="executeDate" label="实施日期" width="180"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <!--<el-link :underline="false" type="info" @click.stop="targetManege(scope.row)">指标管理</el-link>-->
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 推送对话框 -->
      <el-dialog title="数据推送" :visible.sync="pushDialogFormVisible" width="30%">
        <el-form :model="pushForm">
          <el-form-item label="推送平台" :label-width="formLabelWidth">
            <el-select v-model="pushForm.region" placeholder="请选择推送平台">
              <el-option :label="item.pushName" :value="item.id" v-for="(item, index) in platformList" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="pushDialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="pushStand">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 标识对话框 -->
      <el-dialog title="添加标识" :visible.sync="identifyDialogFormVisible" width="30%">
        <el-form :model="identifyForm">
          <el-form-item label="标识名称" :label-width="formLabelWidth">
            <el-input v-model="identifyForm.identifyName" placeholder="请输入标识名称"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="identifyDialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="addSign">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      queryForm: {
        keyWord: '',
        code: '',
        type: '1',
        ppbj: '1',
        status: '',
        startYear: '',
        startExecuteDate: '',
        csdc: '',
        ics: '',
        nec: '',
        sign: '',

        size: 10,
        page: 1,
        total: ''
      },
      cnStAdmOptions: [], // 中国标准分类管理
      inStAdmOptions: [], // 国际标准分类号
      isHandPick: false,

      tableData: [],
      multipleSelection: [],
      editInfo: {},

      formLabelWidth: '120px',
      pushForm: { region: '' }, // 推送
      identifyForm: { identifyName: '' }, // 标识
      platformList: [],
      pushDialogFormVisible: false,
      identifyDialogFormVisible: false,

      cnprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.cnProps
      },
      inprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.inProps
      },
      necprops: {
        lazy: true,
        checkStrictly: true,
        lazyLoad: that.necProps
      },
      innodes: [],
      cnnodes: [],
      nenodes: []
    }
  },
  methods: {
    pushStand() {
      let standardIdList = []
      for (var i = 0; i < this.multipleSelection.length; i++) {
        standardIdList.push(this.multipleSelection[i].id)
      }
      let params = {
        standardIdList: standardIdList,
        platId: this.pushForm.region
      }

      this.httpPost('/push/standard/add', params).then(ret => {
        this.$message({ type: 'success', message: '推送成功!' })
        this.loadData()
        this.pushDialogFormVisible = false
      })
    },
    platform() {
      this.httpGet('/push/platform/queryAll', '').then(res => {
        this.platformList = res
      })
    },
    // 新增
    add() {
      this.$router.push({ path: '/editStandard', name: 'EditStandard', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editStandard', name: 'EditStandard', params: { type: 'edit', info } })
    },

    //查看
    detail(id) {
      this.$router.push({ path: '/standardDetail', name: 'StandardDetail', params: { id } })
      this.$sessionData('set', 'stand_id', id)
    },

    // 指标管理
    targetManege(info) {
      this.$router.push({ path: '/targetManege', name: 'TargetManege', params: { info } })
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/standard/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
          let standardVideoSort = [7, 6]
          // this.httpPost('/video/sort', standardVideoSort).then(ret => {})
        })
        .catch(() => {})
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let idList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            idList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/standard/rem/batch', idList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 批量推送
    push() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要推送的数据！')
      this.pushDialogFormVisible = true
    },

    // 批量标识
    identify() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要标识的数据！')
      this.identifyDialogFormVisible = true
    },
    addSign() {
      let standardIdList = []
      for (var i = 0; i < this.multipleSelection.length; i++) {
        standardIdList.push(this.multipleSelection[i].id)
      }
      let params = {
        standardIdList: standardIdList,
        sign: this.identifyForm.identifyName
      }
      this.httpPost('/standard/markSign', params).then(ret => {
        this.$message({ type: 'success', message: '设置成功!' })
        this.loadData()
        this.identifyDialogFormVisible = false
      })
    },

    // 批量清除标识
    clearIdentify() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要清除的数据！')
      this.$confirm('此操作将清除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let standardIdList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            standardIdList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/standard/clearSign', standardIdList).then(ret => {
            this.$message({ type: 'success', message: '清除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 选择日期改变
    dateChange(e, flag) {
      if (flag === 1) {
        this.queryForm.startExecuteDate = this.$moment(e).format('YYYYMMDDhhmmss')
      } else {
        this.queryForm.startYear = this.$moment(e).format('YYYY')
      }
    },

    onSubmit() {
      console.log(this.queryForm)

      this.loadData()
    },

    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row)
        })
      } else {
        this.$refs.multipleTable.clearSelection()
      }
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    handleCnChange(value) {
      console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.cnnodes.map(item => {
        if (item.value === id) {
          console.log(item)
          this.queryForm.csdc = item.value
        }
      })
      this.hiddenDom(1)
    },

    handleInChange(value) {
      console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.innodes.map(item => {
        if (item.value === id) {
          this.queryForm.ics = item.value
        }
      })
      this.hiddenDom(1)
    },

    handleNeChange(value) {
      console.log(value)
      let val = Object.assign([], value)
      let id = val.pop()
      this.nenodes.map(item => {
        if (item.value === id) {
          this.queryForm.nec = item.value
        }
      })
      this.hiddenDom(1)
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    // 是否精选条件
    chooseCondition() {
      this.isHandPick = !this.isHandPick
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/standard/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    },

    cnProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 1 }
      this.setVal(resolve, params, 'cnnodes')
    },
    inProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 2 }
      var nodes = []
      this.setVal(resolve, params, 'innodes')
    },
    necProps(node, resolve) {
      const { level, data } = node
      let parentId = data ? data.cateId : 0
      let params = { parentId: parentId, type: 3 }
      var nodes = []
      this.setVal(resolve, params, 'nenodes')
    },
    setVal(resolve, params, nodetype) {
      var nodes = []
      this.httpGet('/standard/cate/query/', params).then(res => {
        // console.log(res)
        if (!res) return
        this.hiddenDom(2)

        for (let item of res) {
          nodes.push({ value: item.fullCode, label: item.cateName, cateId: item.id })
          this[nodetype].push({ value: item.fullCode, label: item.cateName, cateId: item.id })
        }
        // console.log(this.nodes)
        resolve(nodes)
      })
    },
    hiddenDom(type) {
      // type  1   2
      setTimeout(() => {
        if (type === 1) {
          var el = document.getElementsByClassName('el-scrollbar')
          var doms = el[el.length - 1].getElementsByTagName('div')
          for (let i = 0; i < doms.length; i++) {
            const e = doms[i]
            if (e.innerHTML === '暂无数据') {
              el[el.length - 1].style.display = 'none'
            }
          }
        } else {
          var el = document.getElementsByClassName('el-scrollbar')
          for (let i = 0; i < el.length; i++) {
            el[i].style.display = 'block'
          }
        }
      }, 0)
    }
  },

  mounted() {
    this.loadData()
    this.platform()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
}
</style>
