<template>
  <div>
    <div class="MT_table_cont">
      <el-table :data="tableData" style="width: 100%;margin-bottom: 20px;" lazy :load="load" row-key="id" :tree-props="{ children: 'children', hasChildren: 'hasChildren' }">
        <el-table-column prop="fullCode" label="完整分类号"> </el-table-column>
        <el-table-column prop="cateName" label="文件/文件夹名称"> </el-table-column>
        <el-table-column prop="cateLevel" label="层级">
          <template slot-scope="scope">
            <span v-if="scope.row.cateLevel === 1"><el-tag>一级</el-tag></span>
            <span v-else-if="scope.row.cateLevel === 2"><el-tag type="success">二级</el-tag></span>
            <span v-else-if="scope.row.cateLevel === 3"><el-tag type="info">三级</el-tag></span>
            <span v-else-if="scope.row.cateLevel === 4"><el-tag type="warning">四级</el-tag></span>
            <span v-else><el-tag type="danger">五级</el-tag></span>
          </template>
        </el-table-column>

        <!-- 操作 -->
        <el-table-column label="操作" width="140">
          <template slot-scope="scope">
            <el-link :underline="false" type="info" @click.stop="add(scope.row)">添加</el-link>
            <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
            <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog :title="type === 1 ? '新建标准分类' : '修改标准分类'" :visible.sync="dialogVisible" width="40%">
      <el-divider></el-divider>

      <b ref="titleDialog" style="display: none">标题</b>
      <el-form v-if="dialogVisible" :model="form" :rules="rules" ref="form" label-width="140px" class="demo-ruleForm">
        <el-form-item label="上级标准分类">
          <el-input disabled v-model="form.cnStClassifyAdm" placeholder="请选择上级标准分类"></el-input>
          <el-link @click.stop="loadCascader">{{ '选择'}}</el-link>
        </el-form-item>
        <el-form-item label="完整分类号" prop="fullCode">
          <el-input v-model="form.fullCode" placeholder="请输入标识"></el-input>
        </el-form-item>
        <el-form-item label="标准分类名称" prop="cateName">
          <el-input v-model="form.cateName" placeholder="请输入被代替的标准"></el-input>
        </el-form-item>
        <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('form')">保存</el-button>
            <el-button @click="resetForm('form')">返回</el-button>
          </el-form-item> -->
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('form')">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 分类选择 -->
    <el-dialog title="分类选择" :visible.sync="casLog" width="30%">
      <el-divider></el-divider>
      <el-cascader v-if="casLog" :show-all-levels="false" @change="handleChange" v-model="options" ref="cascaderHandle" :props="props" clearable></el-cascader>

      <span slot="footer" class="dialog-footer">
        <el-button @click="casLog = false">取 消</el-button>
        <el-button type="primary" @click="casLog = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['status'],
  data() {
    let that = this
    return {
      tableData: [],
      queryForm: {
        parentId: 0,
        type: this.status,
        size: 10,
        page: 1,
        total: ''
      },
      type: 1, // 1新增 2修改
      dialogVisible: false,
      form: {
        cnStClassifyAdm: '',
        fullCode: '',
        cateName: '',
        type: this.status,
        parentId: ''
      },
      rules: {
        cnStClassifyAdm: [
          { required: true, message: '请选择上级标准分类', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        fullCode: [
          { required: true, message: '请输入完整分类号', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ],
        cateName: [
          { required: true, message: '请输入标准分类名称', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ]
      },
      options: [],
      props: {
        lazy: true,
        checkStrictly: true,
        lazyLoad(node, resolve) {
          const { level, data } = node
          let parentId = data ? data.value : 0
          let params = { parentId: parentId, type: this.status }
          var nodes = []
          that.httpGet('/standard/cate/query/', params).then(res => {
            // console.log(res)
            if (!res) return
            that.hiddenDom(2)

            for (let item of res) {
              nodes.push({ value: item.id, label: item.cateName })
              that.nodes.push({ value: item.id, label: item.cateName })
            }
            // console.log(that.nodes)
            resolve(nodes)
          })
        }
      },
      casLog: false,
      nodes: []
    }
  },
  methods: {
    load(tree, treeNode, resolve) {
      console.log(tree, treeNode, resolve)
      this.queryForm.parentId = tree.id
      this.queryForm.total = ''
      this.httpGet('/standard/cate/query/', this.queryForm).then(res => {
        let data = Object.assign(res)
        data.map(item => {
          if (item.childrenCount) {
            item.hasChildren = true
          }
        })
        resolve(data)
      })
    },
    loadCascader() {
      this.casLog = true
    },
    // 添加
    add(info) {
      this.type = 1
      this.dialogVisible = true
      this.form = {
        cnStClassifyAdm: info.cateName,
        fullCode: '',
        cateName: '',
        type: this.status,
        parentId: info.id
      }
    },

    // 修改
    edit(info) {
      this.type = 2
      this.dialogVisible = true
      this.httpGet('/standard/cate/detail/' + info.id, this.queryForm).then(res => {
        console.log(this.form)
        if (res.parentId == 0) {
          this.form = res
          return
        }
        this.httpGet('/standard/cate/detail/' + res.parentId, this.queryForm).then(res1 => {
          if (!res1) return
          this.form = res
          this.form.cnStClassifyAdm = res1.cateName
          console.log(this.form.cnStClassifyAdm)
        })
      })
    },

    // 删除
    del(inid) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/standard/cate/rem/' + inid, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.queryForm.parentId = 0
            this.loadData()
          })
        })
        .catch(() => {})
    },
    submitForm(formName) {
      let that = this
      this.$refs[formName].validate(valid => {
        if (valid) {
          var params = Object.assign({}, that.form)
          console.log(params)
          if (that.type == 1) {
            that.httpPost('/standard/cate/add', params).then(res => {
              that.$message.success('增加成功')
              that.dialogVisible = false
              that.queryForm.parentId = 0
              that.loadData()
            })
          } else {
            that
              .httpPost('/standard/cate/mod/' + that.form.id, params)
              .then(res => {
                that.$message.success('修改成功')
                that.queryForm.parentId = 0
                that.loadData()
              })
              .catch(err => {})
          }
          that.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    },

    handleChange(value) {
      console.log(value)
      console.log(this.nodes)
      let val = Object.assign([], value)
      this.form.parentId = val.pop()
      this.nodes.map(item => {
        if (item.value === this.form.parentId) {
          this.form.cnStClassifyAdm = item.label
        }
      })
      this.hiddenDom(1)
    },

    hiddenDom(type) {
      setTimeout(() => {
        if (type === 1) {
          var el = document.getElementsByClassName('el-scrollbar')
          var doms = el[el.length - 1].getElementsByTagName('div')
          for (let i = 0; i < doms.length; i++) {
            const e = doms[i]
            if (e.innerHTML === '暂无数据') {
              el[el.length - 1].style.display = 'none'
            }
          }
        } else {
          var el = document.getElementsByClassName('el-scrollbar')
          for (let i = 0; i < el.length; i++) {
            el[i].style.display = 'block'
          }
        }
      }, 0)
    },

    loadData() {
      this.queryForm.total = ''
        this.tableData = []
      this.httpGet('/standard/cate/query/', this.queryForm).then(res => {
        let data = Object.assign(res)
        data.map(item => {
          if (item.childrenCount) {
            item.hasChildren = true
          }
        })
        this.tableData = data
        console.log(this.tableData)
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.el-form-item {
  width: 500px;
}
.el-cascader {
  width: 360px;
}
/deep/.el-form-item__content {
  display: flex;
  .el-link {
    width: 50px;
  }
}
</style>
