<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>标准确认管理</el-breadcrumb-item>
        <el-breadcrumb-item>确认操作2</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="委托单位名称" prop="keyword">
            <el-input v-model="queryForm.keyword" placeholder="请输入搜索内容" clearable></el-input>
          </el-form-item>
          <!-- <div class="tips">默认显示30天内数据</div> -->
          <el-form-item label="" prop="chooseDate">
            <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <!-- <el-button @click.stop="add" icon="el-icon-plus">新增</el-button> -->
            <span> 共有 <i style="color: #f00">{{ queryForm.total }}</i> 个用户托管，托管标准共 <i style="color: #f00">{{ count }}</i> 个。 </span>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <!-- <span @click.stop="exportData">导出当前数据</span> -->
            </div>
          </div>
        </div>

        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
          <el-table-column prop="rownum_" label="序号" width="100"> </el-table-column>
          <el-table-column prop="company" label="委托单位"> </el-table-column>
          <el-table-column prop="confirmNumber" label="确认委托个数"> </el-table-column>
          <el-table-column prop="contractName" label="委托人"> </el-table-column>
          <el-table-column prop="contractMethod" label="联系方式"> </el-table-column>
          <el-table-column prop="submitDate" label="委托日期"> </el-table-column>
          <el-table-column label="状态" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status === 1">已保存</el-tag>
              <el-tag v-else type="danger">未保存</el-tag>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="130">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="check(scope.row, 'edit')">修改</el-link>
              <el-link :underline="false" type="primary" @click.stop="check(scope.row, 'search')">审查</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 标准确认 -->

      <el-dialog :title="btnType === 'edit' ? '标准确认修改' : '标准确认检索'" :visible.sync="dialogVisible" width="80%" :before-close="handleClose">
        <el-divider></el-divider>

        <!-- 表格 -->
        <table-data v-if="dialogVisible" :tableData="tableDetail" :userType="userType" :flag="flag" :btnType="btnType" @editTable="editTable" @report="report" @research="research"></table-data>
        <!-- 表格 -->

        <span slot="footer" class="dialog-footer">
          <!-- <el-button @click="dialogVisible = false">取 消</el-button> -->
          <el-button type="success" @click="submit">提 交</el-button>
          <el-button type="primary" @click="save">保 存</el-button>
        </span>
      </el-dialog>

      <!-- 新增确认单 -->

      <el-dialog title="修改确认单" :visible.sync="newConfirmDialog" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="委托单位" prop="company">
            <el-input v-model="ruleForm.company"></el-input>
          </el-form-item>
          <el-form-item label="委托人" prop="contractName">
            <el-input v-model="ruleForm.contractName"></el-input>
          </el-form-item>
          <el-form-item label="联系方式" prop="contractMethod">
            <el-input v-model="ruleForm.contractMethod"></el-input>
          </el-form-item>
          <el-form-item v-if="!isEdit" label="导入标准" prop="stdCodeList">
            <el-upload class="upload-demo" accept=".xlsx,.xls" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeExcelUpload">
              <i class="el-icon-upload"></i>
              <div class="el-upload__text"><i class="el-icon-upload2"></i> 导入</div>
            </el-upload>
            <el-button type="primary" @click.stop="downModule" icon="el-icon-download">模板下载</el-button>
          </el-form-item>
          <el-table v-if="!isEdit" class="report_data" :data="ruleForm.stdCodeList" style="width: 100%">
            <el-table-column type="index" label="序号" width="50"> </el-table-column>
            <el-table-column prop="code" label="标准号">
              <template slot-scope="scope"> {{ ruleForm.stdCodeList[scope.$index] }} </template>
            </el-table-column>
            <el-table-column label="操作" width="80">
              <template slot-scope="scope">
                <el-link :underline="false" type="danger" @click.stop="delDetail(scope.$index)">删除</el-link>
              </template>
            </el-table-column>
          </el-table>

        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="newConfirmSubmit('ruleForm')">保 存</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import tableData from '@/components/confirm/Confirm'
export default {
  components: { tableData },
  data() {
    var validateTabel = (rule, value, callback) => {
      // console.log(rule, value)
      if (!this.ruleForm.stdCodeList.length) {
        callback(new Error('请导入标准'))
      } else {
        callback()
      }
    }
    return {
      queryForm: {
        keyword: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        total: '',
        roleCode: '10003'
      },
      count: '',
      tableData: [],
      tableDetail: [],
      dialogVisible: false,
      Info: {}, // 当前查看项
      detailPort: false, // 是否可以导出

      newConfirmDialog: false,
      ruleForm: {
        id: '',
        company: '',
        contractName: '',
        contractMethod: '',
        stdCodeList: []
      },
      rules: {
        company: [{ required: true, message: '请输入委托单位', trigger: 'blur' }],
        contractName: [{ required: true, message: '请输入委托人', trigger: 'blur' }],
        contractMethod: [{ required: true, message: '请输入联系方式', trigger: 'blur' }],
        stdCodeList: [{ required: true, validator: validateTabel, trigger: 'blur' }]
      },
      userType: 2, // 1管理员 2确认管理1
      flag: 'create', // create-创建，confirm-已检索，check-已审核，approve-已批准
      btnType: 'edit', //  edit修改 search检索
      isEdit: false
    }
  },

  methods: {
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      // console.log(this.queryForm)
    },

    // 搜索
    onSubmit() {
      // console.log(this.queryForm)
      this.loadData()
    },

    add() {
      this.newConfirmDialog = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm.stdCodeList = []
      })
    },

    // 修改  已保存
    // 查看
    check(info, type) {
      this.isEdit = true
      if (type === 'edit') {
        this.newConfirmDialog = true
        this.$nextTick(() => {
          this.$refs.ruleForm.resetFields()
          this.ruleForm = info
          this.ruleForm.stdCodeList = []
          this.ruleForm.id = info.id
        })
      } else {
        info.type = 'conf2'
        this.$router.push({ path: '/affirmDetail', name: 'AffirmDetail', params: { info: info } })
        this.$sessionData('set', 'affirmInfo', info)
      }
    },
    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/standard/ack/rem/' + id, {}).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate

      console.log(params)
      this.httpGet('/standard/ack/query', params).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.count = res.ext1
        this.queryForm.total = res.total
      })
    },

    // 列表导出
    exportData() {
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      params.total = ''
      let paramArr = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/standard/ack/export?' + paramArr.join('&'))
    },
    // 修改导出
    report() {
      if (!this.detailPort) return this.$message.warning('暂无数据导出'), false
      window.open(this.$baseRequestUrl + '/standard/ack/result/report/download/1?reportSequence=2')
      this.dialogVisible = false
    },

    // 添加  ****************************************************************************************************************
    // 模板下载
    downModule() {
      window.open(this.$templateDownloadUrl + 2)
    },
    delDetail(index) {
      this.ruleForm.stdCodeList.splice(index, 1)
    },
    uploadAction() {
      // return this.$picUpload
      return this.$ExcelUpload
    },

    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.import(res.data.fileUrl)
    },

    // 解析标准号
    import(fileUrl) {
      console.log(fileUrl)
      this.httpGet('/standard/ack/parseCode?fileUrl=' + fileUrl, '')
        .then(res => {
          this.ruleForm.stdCodeList = res
        })
        .catch(err => {})
    },
    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: '上传文件大小不能超过 100MB!',
          type: 'warning'
        })
      }
      return isLt2M
    },

    // 修改确认保存
    newConfirmSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 修改
          let params = Object.assign({}, this.ruleForm)
          // params.
          console.log(params)
          this.httpPost('/standard/ack/mod/' + this.ruleForm.id, params).then(res => {
            console.log(res)
            this.$message.warning('修改成功')
            this.loadData()
            this.newConfirmDialog = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 弹窗检索 888***********************************************************************************************************

    loadDetail(id) {
      this.httpGet('/standard/ack/result/query/' + id, {}).then(res => {
        // console.log(res)
        this.detailPort = res.length ? true : false
        this.tableDetail = res
      })
    },

    save() {
      let compareVos = this.tableDetail
      this.httpPost('/standard/ack/result/save/' + this.info.id, compareVos)
        .then(res => {
          this.$message({ type: 'success', message: '保存成功!' })
          this.isEdit = false
        })
        .catch(err => {})
    },

    submit() {
      let compareVos = this.tableDetail
      this.httpPost('/standard/ack/result/confirm/' + this.info.id, compareVos).then(res => {
        // console.log(res)
      })
      // .catch(err => {})
    },

    // 修改弹窗表格
    editTable(e) {
      console.log(e)
      this.tableDetail = e
      this.isEdit = true
    },
    // 重新检索
    research() {
      this.loadDetail(this.info.id)
      this.isEdit = false
    },
    handleClose(done) {
      if (this.isEdit) {
        this.$confirm('有修改操作， 请先保存再关闭')
          .then(_ => {})
          .catch(_ => {})
      } else {
        done()
      }
    }
  },

  mounted() {
    const now = new Date()
    const endDate = now
    const startDate = now - 8.64e7 * 30

    this.queryForm.endDate = this.$moment(endDate).format('YYYYMMDD')
    this.queryForm.startDate = this.$moment(startDate).format('YYYYMMDD')
    this.queryForm.chooseDate = [startDate, endDate]
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.tips {
  display: inline-block;
  line-height: 40px;
  color: #bbb;
  margin-right: 20px;
}
.el-range-editor {
  width: 300px;
}
/deep/#detail {
  .cell {
    text-align: center;
  }
}
.demo-ruleForm {
  width: 500px;
}
.report_data {
  padding-top: 20px;
  margin-left: 20px;
}
</style>
