<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>游客下载管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <!-- <div class="tips">默认显示30天内数据</div> -->
          <el-form-item label="" prop="keywords">
            <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="keywords">
            <el-input v-model="queryForm.keywords" placeholder="请输入搜索内容"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <div class="btn_cont">
            <span> 共有 <i style="color: #f00">{{ queryForm.total }}</i> 条标准被下载，下载次数共 <i style="color: #f00">{{ count }}</i> 次。 </span>
            <div class="choose_btn _color">
              <span @click.stop="exportData">导出当前数据</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <!-- <el-table-column type="selection" width="55"> </el-table-column> -->
          <el-table-column prop="code" label="标准号" width="180"> </el-table-column>
          <el-table-column prop="csName" label="标准名称"> </el-table-column>
          <el-table-column prop="clickNum" label="点击数" width="100"> </el-table-column>
          <el-table-column prop="downloadCount" label="下载次数" width="100"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row.standardId)">查看</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <el-dialog title="下载详情" :visible.sync="dialogVisible" width="50%">
        <el-divider></el-divider>
        <el-table :data="downloadDetail" style="width: 100%">
          <el-table-column prop="ipAddress" label="下载ip地址"> </el-table-column>
          <el-table-column prop="createTime" label="最近下载时间" width="180"> </el-table-column>
        </el-table>

        <div class="MT_pagination" v-if="queryFormDetail.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryFormDetail, loadDetail)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryFormDetail, loadDetail)">尾页</span>
          <el-pagination
            background
            :total="queryFormDetail.total"
            :current-page.sync="queryFormDetail.page"
            :page-size.sync="queryFormDetail.size"
            @current-change="loadDetail"
            layout="total, prev, pager, next, jumper"
          />
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        keywords: '',
        chooseDate: [],
        startDate: '',
        endDate: '',
        size: 10,
        page: 1,
        total: '',
        type:2
      },
      count: '',
      queryFormDetail: {
        size: 10,
        page: 1,
        total: '',
        standardId:''
      },
      sdid:'',
      tableData: [],
      downloadDetail: [],
      dialogVisible: false
    }
  },

  methods: {
    // 查看
    edit(id) {
      this.dialogVisible = true
      this.sdid = id
      this.loadDetail()
    },

    changeDate(e) {
      if(!e){
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      }else{
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDDhhmmss')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDDhhmmss')
      }
    },
    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    loadData() {
      this.queryForm.total = ''
      this.changeDate(this.queryForm.chooseDate)
      let params = Object.assign({}, this.queryForm)
      delete params['chooseDate']
      this.httpGet('/standard/download/query/', params).then(res => {
        this.tableData = res.rows
        this.count = res.ext1
        this.queryForm.total = res.total
      })
    },

    loadDetail(id) {
      this.queryFormDetail.standardId = this.sdid
      this.httpGet('/standard/download/guest/query',this.queryFormDetail).then(res => {
        this.downloadDetail = res.rows
        this.queryFormDetail.total = res.total
      })
    },
    exportData() {
      this.queryForm.keyword = this.queryForm.keyword?this.queryForm.keyword:''
      window.open(this.$baseRequestUrl + '/standard/download/export?type=2&keyword='+this.queryForm.keyword+'&startDate='+this.queryForm.startDate+'&endDate='+this.queryForm.endDate)
    }
  },

  mounted() {
    const now = new Date()
    const endDate = now - 8.64e7
    const startDate = now - 8.64e7 * 30

    this.queryForm.endDate = this.$moment(endDate).format('YYYY-MM-DD')
    this.queryForm.startDate = this.$moment(startDate).format('YYYY-MM-DD')
    this.queryForm.chooseDate = [startDate, endDate]
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.el-range-editor {
  width: 300px;
}
</style>
