<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/standard' }">{{ parentMenu }}</el-breadcrumb-item>
        <el-breadcrumb-item>标准详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="item_cont">
        <div style="margin-bottom: 20px ">
          <div class="cnname">{{ ruleForm.csName }}</div>
          <div class="egname">{{ ruleForm.egName }}</div>
          <el-tag v-if="ruleForm.type === 1" type="primary">国家标准</el-tag>
          <el-tag v-else-if="ruleForm.type === 2" type="primary">行业标准</el-tag>
          <el-tag v-else-if="ruleForm.type === 3" type="primary">地方标准</el-tag>
          <el-tag v-else-if="ruleForm.type === 4" type="primary">国际标准</el-tag>
          <el-tag v-else type="primary">团体标准</el-tag>
          <el-tag style="margin-left: 20px" v-if="ruleForm.status === 'A'" type="success">现行</el-tag>
          <el-tag style="margin-left: 20px" v-else type="danger">作废</el-tag>
        </div>
        <div v-if="ruleForm.type === 5" style="margin-bottom: 40px">
          <div class="cont_title">团体信息</div>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">团体名称</el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.name }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">标准编号</el-col>
                <el-col :span="20">{{ ruleForm.code }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">登记证号</el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.certNo }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">发布日期</el-col>
                <el-col :span="20">{{ ruleForm.publishDate }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">发证机关</el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.certOrg }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">邮编</el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.zipCode }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">法人</el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.legalPerson }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">地址</el-col>
                <el-col :span="20">{{ ruleForm.teamInfo.address }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="10">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">包含专利信息</el-col>
                <el-col :span="20">
                  <span v-if="ruleForm.teamInfo.patent">是</span>
                  <span>否</span>
                </el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="14">
              <div class="grid-content bg-purple">
                <el-col :span="4" class="title">标准状态</el-col>
                <el-col :span="20">
                  <span v-if="status === 'A'">现行</span>
                  <span v-else>作废</span>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title">业务范围</el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.teamInfo.serviceRange }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title">起草人</el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.drafter }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title">起草单位</el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.draftingUnit }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="24">
              <div class="grid-content bg-purple">
                <el-col :span="2" class="title">主要技术内容</el-col>
                <el-col :span="22">
                  <div>{{ ruleForm.mainContent }}</div>
                </el-col>
              </div>
            </el-col>
          </el-row>
        </div>
        <div v-else style="margin-bottom: 40px">
          <div class="cont_title">基本信息</div>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title">标准号</el-col>
                <el-col :span="19">{{ ruleForm.code }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title">发布年代</el-col>
                <el-col :span="19">{{ ruleForm.year }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title">实施日期</el-col>
                <el-col :span="19">{{ ruleForm.executeDate }}</el-col>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title">标准状态</el-col>
                <el-col :span="19">{{ ruleForm.status == 'A' ? '现行' : '作废' }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title">有效日期</el-col>
                <el-col :span="19">{{ ruleForm.expiryDate }}</el-col>
              </div>
            </el-col>
            <el-col style="border-bottom: 1px solid #ddd;" :span="8">
              <div class="grid-content bg-purple">
                <el-col :span="5" class="title">全文页数</el-col>
                <el-col :span="19">{{ ruleForm.page }}</el-col>
              </div>
            </el-col>
          </el-row>
        </div>
        <div style="margin-bottom: 40px">
          <div class="cont_title">标准分类</div>
          <el-row class="classify" :gutter="20">
            <el-col :span="2">中国标准分类</el-col>
            <el-col :span="20">{{ ruleForm.csdc }}</el-col>
          </el-row>
          <el-row class="classify" :gutter="20">
            <el-col :span="2">国际标准分类</el-col>
            <el-col :span="20">{{ ruleForm.ics }}</el-col>
          </el-row>
          <el-row v-if="ruleForm.type === 5" class="classify" :gutter="20">
            <el-col :span="2">国民经济分类</el-col>
            <el-col :span="20">{{ ruleForm.nec }}</el-col>
          </el-row>
        </div>
        <div style="margin-bottom: 40px">
          <div class="cont_title">适用范围</div>
          <div style="padding: 0 20px; line-height: 30px">{{ ruleForm.sdRange }}</div>
        </div>
        <el-button @click="resetForm('ruleForm')">返回</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    let that = this
    return {
      ruleForm: {
        code: '',
        id: '',
        csName: '',
        egName: '',
        type: '', //  '' 全部  1行业 2地方 3国家 4国际
        standText: '', //  '' 全部  0包含 1不包含
        status: '', // '' 全部  0作废 ''现形
        year: '',
        executeDate: '',
        expiryDate: '',
        csdc: '',
        ics: '',
        replaceSd: '',
        replacedSd: '',
        sdRange: '',
        sign: '',
        cnVal: '',
        inVal: ''
      },
      parentMenu: ''
    }
  },
  methods: {
    resetForm(formName) {
      this.$router.back()
      this.$refs[formName].resetFields()
    }
  },
  mounted() {
    const { params } = this.$route
    console.log(params)
    let id = this.$sessionData('get', 'stand_id')
    this.parentMenu = params.type == '2' ? '标准推送管理' : '标准管理'
    this.httpGet('/standard/detail/' + id, '').then(res => {
      this.ruleForm = res
      this.ruleForm.cnVal = res.csdc
      this.ruleForm.inVal = res.ics
    })
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item {
  .span {
    width: 300px !important;
  }
  .el-textarea {
    width: 300px;
  }
}
.el-cascader {
  width: 300px;
}
.el-form-item {
  margin-bottom: 0 !important;
}
/deep/.el-form-item__label {
  line-height: 36px;
}
/deep/.el-form-item__content {
  line-height: 36px;
}

.item_cont {
  padding: 20px 50px;
  .cnname,
  .egname {
    margin-bottom: 10px;
  }
  .el-col {
    line-height: 40px;
    .title {
      text-align: right;
    }
  }
  .cont_title {
    border-bottom: 1px solid #ddd;
    padding-left: 20px;
    border-left: 3px solid blue;
    padding: 10px 20px;
    font-weight: bold;
  }
  .classify {
    margin-left: 0 !important;
    margin-right: 0 !important;
    border-bottom: 1px solid #ddd;
  }
}
</style>
