<template>
  <div class="MT_table_cont item">
    <!-- 按钮 -->
    <div class="btns" v-if="this.btnType === 'edit'">
      <el-button @click.stop="search" icon="el-icon-refresh-right">重新检索</el-button>
      <div class="btn_cont">
        <div class="choose_btn _color">
          <span @click.stop="exportData">导出数据</span>
        </div>
      </div>
    </div>
    <el-table border id="detail" :data="tableData" style="width: 100%">
      <el-table-column prop="rownum_" label="序号" width="60"> </el-table-column>
      <el-table-column prop="originalName" label="申请确认标准(方法)名称" show-overflow-tooltip> </el-table-column>
      <el-table-column prop="originalNo" label="申请确认标准(方法)编号(含年号)" width="230"> </el-table-column>
      <el-table-column prop="resultName" label="检索结果标准(方法)名称" show-overflow-tooltip> </el-table-column>
      <el-table-column prop="resultNo" label="检索结果标准(方法)编号(含年号)" width="230"> </el-table-column>
      <el-table-column prop="validateType" label="确认结论" width="100"> </el-table-column>
      <el-table-column prop="comments" label="备注" width="120" show-overflow-tooltip> </el-table-column>
      <!-- 操作 -->
      <el-table-column label="操作" width="80" v-if="this.btnType === 'edit'">
        <template slot-scope="scope">
          <el-link :underline="false" type="primary" @click.stop="edit(scope.row, scope.$index)">修改</el-link>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="修改标准" :visible.sync="dialogVisible" width="30%" append-to-body>
      <el-divider></el-divider>
      <!-- form表单 -->
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="160px" class="demo-ruleForm">
        <el-form-item label="申请确认编号(含年号)" prop="originalNo">
          <el-input v-model="ruleForm.originalNo" placeholder="请输入申请确认标准(方法)编号(含年号)"></el-input>
        </el-form-item>
        <!-- <el-form-item label="检测结果编号(含年号)" prop="resultNo">
          <el-input v-model="ruleForm.resultNo" placeholder="请输入检测结果标准(方法)编号(含年号)"></el-input>
        </el-form-item> -->
        <el-form-item label="确认结论" required>
          <el-select v-model="ruleForm.validateType" placeholder="请选择确认结论">
            <el-option label="A" value="A"></el-option>
            <el-option label="B" value="B"></el-option>
            <el-option label="E" value="E"></el-option>
            <el-option label="F" value="F"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="comments">
          <el-input type="textarea" v-model="ruleForm.comments"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['tableData', 'userType', 'flag', 'btnType'],
  data() {
    return {
      dialogVisible: false,
      ruleForm: {},

      rules: {
        originalNo: [{ required: true, message: '请输入申请确认标准(方法)编号(含年号)', trigger: 'blur' }],
        // resultNo: [{ required: true, message: '请输入检测结果标准(方法)编号(含年号)', trigger: 'blur' }]
      },
      index: 0
    }
  },
  methods: {
    // 重新检索
    search() {
      this.$emit('research')
    },
    exportData() {
      this.$emit('report')
    },
    edit(info, i) {
      this.index = i
      this.ruleForm = Object.assign({}, info)
      this.dialogVisible = true
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {

          let arr = Object.assign([], this.tabelData)
          arr[this.index] = this.ruleForm
          arr.map(item => {item.resultNoAndName = item.resultName ? item.resultName + item.originalNo : item.originalNo})
          this.$emit('editTable', arr)
          // console.log(this.ruleForm)
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },
  mounted() {
    console.log(this.userType) // 1管理员 2确认管理1
    console.log(this.flag) // create-创建，confirm-已检索，check-已审核，approve-已批准
    console.log(this.btnType) //  edit修改 search检索
  }
}
</script>

<style lang="scss" scoped>
.item {
  border: none;
  padding: 0;
  margin-top: -10px;
}
.el-select {
  width: 100%;
}
</style>
