<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准管理</el-breadcrumb-item>
        <el-breadcrumb-item>欧盟数据管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="140px">
        <el-form-item label="英文名称" prop="egName">
          <el-input v-model="queryForm.egName" placeholder="请输入英文名称"></el-input>
        </el-form-item>
        <el-form-item label="文本日期" prop="chooseDate">
          <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="欧盟文本" prop="EUtext">
          <el-select v-model="queryForm.ifDownload" placeholder="请选择欧盟文本">
            <el-option label="全部" value=""></el-option>
            <el-option label="包含" value="1"></el-option>
            <el-option label="不包含" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit">搜索</el-button>
          <el-button @click="resetForm('queryForm')">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <div class="btn_cont">
            <div class="choose_btn _color"></div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="egName" label="标题" :show-overflow-tooltip="true"> </el-table-column>
          <el-table-column prop="author" label="作者" width="150"> </el-table-column>
          <el-table-column label="文本" width="120">
            <template slot-scope="scope">
              {{ scope.row.ifDownload === 1 ? '包含' : '不包含' }}
            </template>
          </el-table-column>
          <el-table-column prop="docDate" label="文本日期" width="120"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">查看</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 详情对话框 -->
      <el-dialog title="详细信息" :visible.sync="dialogVisible" width="50%">
        <el-divider></el-divider>
        <div class="item_cont">
          <div class="item">
            <span class="left">英文标题：</span>
            <span class="right">{{ checkInfo.egName }}</span>
          </div>
          <div class="item">
            <span class="left">作者：</span>
            <span class="right">{{ checkInfo.author }}</span>
          </div>
          <div class="item">
            <span class="left">相关链接：</span>
            <span class="right">{{ checkInfo.aboutLink }}</span>
          </div>
          <div class="item">
            <span class="left">下载链接：</span>
            <span class="right">{{ checkInfo.downLink }}</span>
          </div>
          <div class="item">
            <span class="left">文本日期：</span>
            <span class="right">{{ checkInfo.docDate }}</span>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click.stop="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        egName: '',
        chooseDate: '',
        startDate: '',
        endDate: '',
        ifDownload:'',
        size: 10,
        page: 1,
        total: ''
      },

      checkInfo: {},
      dialogVisible: false,
      tableData: []
    }
  },
  methods: {
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryForm)
    },

    // 修改
    edit(info) {
      this.httpGet('/euLegal/detail/'+info.id, '').then(res => {
        this.checkInfo = res
      })
      this.dialogVisible = true
    },

    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/euLegal/query/', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #ddd;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 300px !important;
  }
  .el-range-editor {
    width: 300px;
  }
}
.item {
  padding: 0 20px 20px;
  display: flex;
  .left {
    flex: 0 0 auto;
    width: 70px;
    text-align: right;
  }
}
</style>
